body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

    .square:focus {
        outline: none;
    }

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.game-info {
    margin-left: 20px;
}

.header-color {
    background: #8e9eab; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #eef2f3, #8e9eab); /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(left, #eef2f3, #8e9eab);
    background: linear-gradient(to right, #eef2f3, #8e9eab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.landing-grid {
    background: #8e9eab; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #eef2f3, #8e9eab); /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(left, #eef2f3, #8e9eab);
    background: linear-gradient(to right, #eef2f3, #8e9eab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}

.praveen-img {
    height: 250px;
    padding-top: 5em;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.banner-text {
    /*background-color:black; */
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}


.banner-text h1 {
    /*color: white;*/
    font-size: 35px;
    font-weight: bold;
}

    .banner-text hr {
        border-top: 1px dashed;  /*dotted white;*/
        width: 50%;
        margin: auto;
    }

    .banner-text p {
        font-size: 20px;
        padding: 1em;
        /*color: white;*/
    }

    .social-links{
       display:-webkit-flex;
       display:flex;
       -webkit-justify-content: space-between;
               justify-content: space-between;
       width:50%;
    }
.social-links i{
    color: white;
    font-size: 5em;
    padding: 8px;
}
